  --color-error-100: #FEE2E2;
  --color-error-200: #FECACA;
  --color-error-300: #FCA5A5;
  --color-error-400: #F87171;
  --color-error-500: #EF4444;
  --color-error-600: #DC2626;
  --color-error-700: #B91C1C;
  --color-error-800: #991B1B;
  --color-error-900: #7F1D1D;

  /* Success Colors */
  --color-success-50: #F0FDF4;
  --color-success-100: #DCFCE7;
  --color-success-200: #BBF7D0;
  --color-success-300: #86EFAC;
  --color-success-400: #4ADE80;
  --color-success-500: #22C55E;
  --color-success-600: #16A34A;
  --color-success-700: #15803D;
  --color-success-800: #166534;
  --color-success-900: #14532D;


  /* Warning Colors */
  --color-warning-50: #FEFCE8;
  --color-warning-100: #FEF9C3;
  --color-warning-200: #FEF08A;
  --color-warning-300: #FDE047;
  --color-warning-400: #FACC15;
  --color-warning-500: #EAB308;
  --color-warning-600: #CA8A04;
  --color-warning-700: #A16207;
  --color-warning-800: #854D0E;
  --color-warning-900: #713F12;





  --space-base: 16px;

  --global-radius-sm: 0.5rem;
  --global-radius-md: 1rem;
  --global-radius-lg: 1.5rem;








  --color-gray-50: #F9F9F9;

  --form-border-color: #dbdbdb;
  --form-border-color-focus: #a0a0a0;
  --form-bg-color: #F8F8F8;
  --error-color: #FE5F5566;
  --correct-color: #cae7be46;
  --color-black-text: #2E2E2E;

  /* Schriftarten */
  --font-space-epilogue: "Epilogue", system-ui, -apple-system, sans-serif;
  
  /* Legacy-Variablen (f체r R체ckw채rtskompatibilit채t) */
  --title-font-family: var(--font-space-epilogue);
  --body-font-family: var(--font-space-epilogue);
  --color-grey-text: #9C9C9C;
  --color-white-text: #ffffff;
  --text-red: red;
  --building_bg_gradient_v1: #FFFEFC;

  --text-primary: #2E2E2E;
  --text-sec: #4C4B49;
  --text-muted: #7F7E79;
  --text-inverse: #FFFFFF;

  --max-width-layout: 92%;
  --radius: 30px;
}


